.auth__auth-link {
  color: #fff;
  box-sizing: border-box;
  padding-top: 15px;
  font-size: 14px;
  font-weight: 400;
  line-height: 100%;
  text-decoration: none;
  transition: opacity .4s linear;
  display: block;
  width: 100%;
  text-align: center;
}

.auth__auth-link:hover {
  opacity: .6;
}